import React from 'react';
import { withRouter } from 'react-router-dom';
import { TabBar } from 'antd-mobile';
class Mytab extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			selectedTab: 'blueTab',
		};
	}
	render() {

		const { pathname } = this.props.location;
		if (pathname.split('/').length>2) {
			return null;
		}
		return (
			<div style={{ position: 'fixed', width: '100%', bottom: 0 }}>
				<TabBar
					unselectedTintColor="#949494"
					tintColor="rgb(255, 133, 42)"
					barTintColor="white"
				>
					<TabBar.Item
						title="首页"
						key="home"
						icon={
							<i className='iconfont icon-Homehomepagemenu'></i>
						}

						selectedIcon={<i className='iconfont icon-Homehomepagemenu1'></i>
						}
						selected={pathname === '/'}
						onPress={() => {
							this.props.history.push('/')
						}}
						data-seed="logId"
					>
					</TabBar.Item>
					<TabBar.Item
						icon={
							<i className='iconfont icon-cart'></i>
						}
						selectedIcon={
							<i className='iconfont icon-cart1'></i>
						}
						title="购物车"
						key="cart"
						selected={pathname === '/cart'}
						onPress={() => {
							this.props.history.push('/cart')
						}}
						data-seed="logId1"
					>
						{/* <Button onClick={()=>{
                    this.props.history.push('/doc')
                }} type='primary'>ToDOC</Button> */}
                购物车
			</TabBar.Item>
					<TabBar.Item
						icon={
							<i className='iconfont icon-education'></i>
						}
						selectedIcon={
							<i className='iconfont icon-List'></i>
						}
						title="订单"
						key="list"
						selected={pathname === '/list'}
						onPress={() => {
							this.props.history.push('/list')
						}}
					>
						订单
			</TabBar.Item>
					<TabBar.Item
						icon={<i className='iconfont icon-ziyuan'></i>}
						selectedIcon={<i className='iconfont icon-wodedangxuan'></i>}
						title="我的"
						key="my"
						selected={pathname === '/my'}
						onPress={() => {
							this.props.history.push('/my')
						}}
					>
						我的
			</TabBar.Item>
				</TabBar>
			</div>
		);
	}
}
export default withRouter(Mytab);