import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';
class Slideshow extends React.Component {
    state = {
        data: ['1', '2', '3', '4', '5', '6', '7', '8'],
        imgHeight: '160px',
    }
    componentDidMount() {
        // simulate img loading
        setTimeout(() => {
            this.setState({
                data: ['https://gw.alicdn.com/imgextra/i2/194/O1CN01YwP8vb1DItSuS5gri_!!194-0-lubanu.jpg',
                    'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg',
                    'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
                    'https://img.alicdn.com/imgextra/i2/96/O1CN014g0ADB1Ca0eWkU60Z_!!96-0-luban.jpg',
                    'https://picasso.alicdn.com/imgextra/i3/382208/O1CNA1iyU1IP100ff4af62860b14de730004_!!382208-0-picassobanner.jpg',
                    'https://gw.alicdn.com/imgextra/i4/119/O1CN01wbwbCC1CkXlJqiaZ7_!!119-0-lubanu.jpg',
                    'https://img.alicdn.com/imgextra/i3/27/O1CN019CAYMI1C4PK6mp9qX_!!27-0-luban.jpg',
                    'https://gw.alicdn.com/imgextra/i4/41/O1CN01lMI3m21CAoruPKN9y_!!41-0-ppp-picassogw.jpg'],
            });
        }, 100);
    }
    render() {
        return (

            <WingBlank style={{ width: '100%', margin: '0px'}}>
                <Carousel
                    autoplay
                    infinite
                >
                    {this.state.data.map(val => (
                        <a
                            key={val}
                            href="http://www.alipay.com"
                            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                            <img
                                src={`${val}`}
                                alt=""
                                style={{ width: '100%', verticalAlign: 'top',height:'170px'}}
                                onLoad={() => {
                                    // fire window resize event to change height
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({ imgHeight: 'auto' });
                                }}
                            />
                        </a>
                    ))}
                </Carousel>
            </WingBlank>

        );
    }
}


export default Slideshow