import React from 'react';
import { withRouter,Link } from 'react-router-dom';
import { TabBar} from 'antd-mobile';
class DetailTab extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			selectedTab: 'blueTab',
		};
	}
	render() {
		return (
			<div style={{width:'100%', position: 'fixed', bottom: 0}}>
				<div style={{ position: 'fixed', width: '40%', bottom: 0 }}>
					<TabBar
						unselectedTintColor="#949494"
						tintColor="rgb(255, 133, 42)"
						barTintColor="white"
					>
						<TabBar.Item
							title="店铺"
							key="shop"
							icon={
								<i className='iconfont icon-dianpu'></i>
							}

							selectedIcon={<i style={{ color: 'rgb(255, 133, 42)' }} className='iconfont icon-dianpu'></i>
							}
							selected={this.state.selectedTab === 'blueTab'}
							onPress={() => {
								this.setState({
									selectedTab: 'blueTab',
								});
							}}
							data-seed="logId4"
						>
						</TabBar.Item>
						<TabBar.Item
							icon={
								<i className='iconfont icon-taobaokefu-01'></i>
							}
							selectedIcon={
								<i style={{ color: 'rgb(255, 133, 42)' }} className='iconfont icon-taobaokefu-01'></i>
							}
							title="淘宝客服"
							key="shoper"
							selected={this.state.selectedTab === 'redTab'}
							onPress={() => {
								this.setState({
									selectedTab: 'redTab',
								});
							}}
							data-seed="logId5"
						>
							{/* <Button onClick={()=>{
                    this.props.history.push('/doc')
                }} type='primary'>ToDOC</Button> */}

						</TabBar.Item>
						<TabBar.Item
							icon={<i className='iconfont icon-shoucang'></i>}
							selectedIcon={<i style={{ color: 'rgb(255, 133, 42)' }} className='iconfont icon-shoucang'></i>}
							title="收藏"
							key="collect"
							selected={this.state.selectedTab === 'purpleTab'}
							onPress={() => {
								this.setState({
									selectedTab: 'purpleTab',
								});
							}}
							data-seed="logId6"
						>

						</TabBar.Item>
					</TabBar>
				</div>
				<div style={{ width: '60%', float: 'right',height:'49px',background:'#fff'}}>
					<div style={{width: '50%',background:'#FF9500',height:'49px',float:'left',textAlign:'center',lineHeight:'49px'}}><Link to='/cart' style={{color:'white'}}>加入购物车</Link></div>
					<div style={{width: '50%',background:'#FF0036',height:'49px',float:'left',textAlign:'center',lineHeight:'49px'}}><Link to='/list' style={{color:'white'}}>立即购买</Link></div>
				</div>
			</div>
		);
	}
}
export default withRouter(DetailTab);